.toolbar{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    background: white;
    height: 56px;
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar__logo{
    margin-left: 1rem;
}
.toolbar__logo a {
color: green;
text-decoration: none;
font-size: 1rem;
}

.toolbar_navigation-items a{
    color: green;
    text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active{
    color: orange;
}

.spacer {
    flex: auto;
}

.toolbar_navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar_navigation-items li{
    padding: 0 0.5rem;
}
.toolbar_navigation a{
    color: green;
    text-decoration: none;

}

@media (max-width: 768px){
    .toolbar_navigation-items{
        display: none;
    }
}

@media (min-width:769px){
    .toolbar__toggle-button{
        display:none;
    }
    .toolbar__logo{
        margin-left: 0;
    }
}